@import url(https://fonts.googleapis.com/css?family=Coming+Soon);
body {
  margin: 0;
  padding: 0;
  font-family: "Coming Soon", cursive;
  background: url(/Hangman/static/media/light.9c0e2408.jpg);
  background-position: center;
  background-size: cover;
}

.App {
  text-align: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Hangman {
  color: black;
  text-align: center;
}

.Hangman img {
  border: 10px solid white;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.Hangman h1 {
  width: 100%;
  background-color: #4CC7FC;
  font-size: 50px;
  font-weight: 500;
  color: #0A1D41;
  margin-bottom: 0.2em;
  box-shadow: 0px 0px 50px #fff;
  border-radius: 5px;
}

#guess {
  margin: 0;
  background-color: cyan;
}

.Hangman-word {
  letter-spacing: 1em;
  margin: 0%;
  font-size: 2rem;
  background-color: aliceblue;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
  color: #fff;
}

.Hangman button {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #0A1D41;
  color: #fff;
  box-shadow: 0px 0px 3px #fff;
  border-radius: 5px;
  padding-top: 4px;
}

.Hangman button:hover {
  background-color: #0A1D41;
  box-shadow: 0px 0px 2px #fff;
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  box-shadow: 0 6px #9e9e9e;
}

#reset {
  width: auto;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
}
